.flexiciousGrid input, select {
  border: none;
  border-bottom: solid 1px #cccccc;
  transition: all 0.3s cubic-bezier(0.64, 0.09, 0.08, 1);
  background: linear-gradient(to bottom, rgba(255, 255, 255, 0) 96%, #cccccc 4%);
  background-position: -200px 0;
  background-size: 200px 100%;
  background-repeat: no-repeat; }
  .flexiciousGrid input:focus, select:focus {
    box-shadow: none;
    outline: none;
    background-position: 0 0; }
    .flexiciousGrid input:focus::-webkit-input-placeholder, select:focus::-webkit-input-placeholder {
      color: #cccccc;
      font-size: 11px;
      -webkit-transform: translateY(-20px);
              transform: translateY(-20px);
      visibility: visible !important; }

