  background: #FFF; }

/* Remove focus */
.flexiciousGrid select:focus {
  outline: none; }

/* Hide label */
.flexiciousGrid .mdl-selectfield label {
  display: none; }

/* Use custom arrow */
.flexiciousGrid .mdl-selectfield {
  font-family: Roboto;
  position: relative; }
  .flexiciousGrid .mdl-selectfield:after {
    position: absolute;
    top: 0.75em;
    right: 0.5em;
    /* Styling the down arrow */
    width: 0;
    height: 0;
    padding: 0;
    content: '';
    border-left: .25em solid transparent;
    border-right: .25em solid transparent;
    border-top: 0.375em solid rgba(0,0,0, 0.12);
    pointer-events: none; }

