position: fixed;
bottom: 12%;
left: 0;
      pointer-events:none;
}
#android_toast{
width: 80%;
margin-left: auto;
margin-right: auto;
background-color: rgb(48, 52, 55);
color: rgb(245, 245, 245);
font-size: 13px;
padding: 5px;
border-radius: 2px;
-webkit-border-radius: 2px;
opacity: 0.95;
box-shadow:  1px 2px 2px 1px rgba(34, 34, 34, 1);
text-align: center;
font-family: sans-serif;
}

#android_toast em{
color: rgb(81, 180, 210);
font-weight: bold;
font-style: normal;
}

.android_toast_top{
top: 12%;
bottom: auto !important;
}


.android_toast_fadein{
animation: android_toast_fadein 1s ease;
-webkit-animation: android_toast_fadein 1s ease;
}

.android_toast_fadeout{
animation: android_toast_fadeout 1s ease;
-webkit-animation: android_toast_fadeout 1s ease;
animation-fill-mode: forwards;
-webkit-animation-fill-mode: forwards;
}

@keyframes android_toast_fadein{
from {
  opacity: 0;
  -webkit-opacity: 0;
}
to {
  opacity: 0.95;
  -webkit-opacity: 0.95;
}
}

@-webkit-keyframes android_toast_fadein{
from {
  opacity: 0;
  -webkit-opacity: 0;
}
to {
  opacity: 0.95;
  -webkit-opacity: 0.95;
}
}

@keyframes android_toast_fadeout{
from {
  opacity: 0.95;
  -webkit-opacity: 0.95;
}
to {
  opacity: 0;
  -webkit-opacity: 0;
}
}

@-webkit-keyframes android_toast_fadeout{
from {
  opacity: 0.95;
  -webkit-opacity: 0.95;
}
to {
  opacity: 0;
  -webkit-opacity: 0;
}
}

.App {
  text-align: center;
  height: inherit;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

